@use 'sass:map';
@import 'colors';
@import 'fonts';
@import 'breakpoints';



/* Box sizing rules */
*,
*::before,
*::after {
    box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
    margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
    list-style: none;
}

/* Set core root defaults */
html:focus-within {
    scroll-behavior: smooth;
}

html,
body {
    height: 100%;
}

/* Set core body defaults */
body {
    text-rendering: optimizeSpeed;
    line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
    text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
    max-width: 100%;
    display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
    font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
    html:focus-within {
        scroll-behavior: auto;
    }

    *,
    *::before,
    *::after {
        animation-duration: 0.01ms !important;
        animation-iteration-count: 1 !important;
        transition-duration: 0.01ms !important;
        scroll-behavior: auto !important;
    }
}

:root {
    --size-100: .25rem;
    --size-200: .5rem;
    --size-300: .75rem;
    --size-400: 1rem;
    --size-500: 1.5rem;
    --size-600: 2rem;
    --size-700: 3rem;
    --size-800: 4rem;
    --size-900: 5rem;


}

/*Utility Classes*/

.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap; /*added line*/
    border: 0;
}

.text-primary-400 {
    color: color(primary-400);
}

.text-accent-400 {
    color: color(accent-400);
}

.text-accent-100 {
    color: color(accent-100);
}

.text-neutral-100 {
    color: color(neutral-100);
}

.text-neutral-900 {
    color: color(neutral-900);
}

.bg-primary-400 {
    background-color: color(primary-400);
}

.bg-accent-400 {
    background-color: color(accent-400);
}

.bg-accent-100 {
    background-color: color(accent-100);
}

.bg-neutral-100 {
    background-color: color(neutral-100);
}

.bg-neutral-900 {
    background-color: color(neutral-900);
}

.fw-bold { font-weight: var(--fw-bold); }
.fw-semi-bold { font-weight: var(--fw-semi-bold); }
.fw-regular { font-weight: var(--fw-regular); }

.fs-primary-heading {
    font-size: var(--fs-primary-heading);
    line-height: 1.2;
    font-weight: fw(bold);
}

.fs-secondary-heading {
    font-size: var(--fs-secondary-heading);
    line-height: 1.2;
    font-weight: fw(bold);
}

.fs-300 { font-size: var(--fs-300); }
.fs-400 { font-size: var(--fs-400); }
.fs-500 { font-size: var(--fs-500); }
.fs-600 { font-size: var(--fs-600); }

.padding-block-700 {
    padding-block: var(--size-700);
}

.padding-block-900 {
    padding-block: var(--size-900);
}

.container {
    --max-width: 1110px;
    --padding: 1rem;

    width: min(var(--max-width), 100% - (var(--padding) * 2));
    margin-inline: auto;
}

.even-columns {
    display: grid;
    gap: 1rem;

    @include mediaQuery(med) {
        grid-auto-flow: column;
        grid-auto-columns: 1fr;
    }
}

/*General styling*/

body {
    font-family: ff(ff-primary);
    font-size: var(--fs-body);
    color: color(primary-400);
}

/* Button */

.button {
    border: 0;
    text-decoration: none;
    border-radius: 100vmax;
    padding: 1.25em 2.5em;
    font-weight: fw(bold);
    font-size: var(--fs-button);
    line-height: 1; /*To make the text centered, then add more padding*/
    cursor: pointer;
    color: color(neutral-100);
    background-color: color(accent-400);
    box-shadow: 0 1.125em 1em -1em color(accent-500);


    &[data-type="inverted"] {
        background-color: color(neutral-100);
        color: color(accent-400);
    }

    &:hover,
    &:focus-visible {
        background-color: color(accent-300);
    }

    &[data-type="inverted"]:hover, 
    &[data-type="inverted"]:focus-visible {
        background-color: color(neutral-100);
        color: color(accent-300);
    }
}

/*Navigation*/

.primary-header {
    margin-top: var(--size-700);
}

.nav-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.mobile-nav-toggle {
    display: none;
}

.mobile-nav-toggle {
    cursor: pointer;
    background: transparent;
    border: 0;
    padding: 0.5em;


    & .icon-close {
        display: none;
    }
    
    @media (max-width: 50em) {
        display: block;
    }
}

.primary-navigation {

    @media (max-width: 50em) {
        display: none;
        position: absolute;
        padding: var(--size-700);
        inset: 7rem var(--size-400) auto;
        background-color: color(neutral-100);
        border-radius: var(--size-100);
        box-shadow: 0 0 0.75em rgba(0, 0, 0, 0.05);
        
        &[data-visible] {
            display: block;
            
        }
        
        &::after {
            content: '';
            position: fixed;
            z-index: -1;
            inset: 0;
            background-image: linear-gradient(red, blue)
        }
    }
}

.nav-list {
    padding: 0;
    margin: 0;
    font-size: var(--fs-nav);
    
    @media (max-width: 50em) {
        display: grid;
        gap: var(--size-600);
        text-align: center;
        font-weight: fw(bold);
        
        & a {
            text-decoration: none;
            color: color(primary-400);
            
            &:hover,
            &:focus-visible {
                color: color(accent-400);
            }
        }
    }
}


